import { Component, OnInit,ViewChild ,ElementRef,EventEmitter} from '@angular/core';
import { IngatlanComponent} from '../ingatlanok/ingatlan';

@Component({
  moduleId: module.id,
  selector: 'app-hunk-slider',
  templateUrl: 'hunk-slider.component.html',
  styleUrls: ['hunk-slider.component.css'],
  inputs: ['movingObjectArray','objWidth'],
  outputs: ['ingatlanSelected'],
  directives:[IngatlanComponent]






})
export class HunkSliderComponent implements OnInit {
  public speed:number=5;
  movingObjectArray:any[]=[];
  state='first';
  currentPos:number=0;
  needPos:number;
  objWidth:number=10;
  ingatlanSelected = new EventEmitter<string>();
  @ViewChild('hunkSlider') hunkSlider:ElementRef;
  //--------------------------CUSTOM----------------------------
  constructor() {}

  ngOnInit() {
  }


  next(){
 //   console.log(this.hunkSlider.nativeElement.clientWidth+this.hunkSlider.nativeElement.scrollLeft);
    let maxWidth=this.hunkSlider.nativeElement.scrollWidth-(this.hunkSlider.nativeElement.clientWidth+this.hunkSlider.nativeElement.scrollLeft);
   // console.log(maxWidth);
    if ( maxWidth<=0)
    {
      this.hunkSlider.nativeElement.scrollLeft=this.hunkSlider.nativeElement.scrollWidth-this.hunkSlider.nativeElement.clientWidth;
    }
    else
    {
      this.needPos=this.currentPos+this.objWidth;
      this.animate();

    }

  }
  prev(){
    if (this.currentPos>0){
      this.needPos=this.currentPos-this.objWidth;
      this.animate();
    }
  }
  animate(){

    if  (Math.abs(this.currentPos-this.needPos)>this.speed)
      setTimeout(() => {
        if (this.currentPos>this.needPos)
          this.currentPos = this.currentPos - this.speed;
        else
          this.currentPos = this.currentPos + this.speed;
        this.animate();
      }, 10);

    this.hunkSlider.nativeElement.scrollLeft=this.currentPos;

  }

  //--------------------------CUSTOM----------------------------
  onIngatlanSelect(ingatlan){
    this.ingatlanSelected.emit(ingatlan);
  }
}
